.notfound {
    position: relative;
    height: 100vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.notfound .notfound-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../images/bg3.jpg');
    background-size: cover;
}

.notfound .notfound-bg:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0%;
    top: 0%;
    background-color: rgba(0, 0, 0, 0.25);
}
  
.notfound .notfound-content {
    position: absolute;
    left: 50%;
    top: 50%;
    max-width: 600px;
    width: 100%;
    text-align: center;
    padding: 30px;
    line-height: 1.4;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
  
.notfound .notfound-content:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50% , -50%);
        -ms-transform: translate(-50% , -50%);
            transform: translate(-50% , -50%);
    width: 100%;
    height: 600px;
    background-color: rgba(255, 255, 255, 0.6);
    -webkit-box-shadow: 0px 0px 0px 30px rgba(255, 255, 255, 0.7) inset;
            box-shadow: 0px 0px 0px 30px rgba(255, 255, 255, 0.7) inset;
    z-index: -1;
}

.notfound-content .notfound-404 {
    position: relative;
    height: 200px;
}
  
.notfound-content .notfound-404 h1 {
    font-family: 'Passion One', cursive;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 220px;
    margin: 0px;
    color: #222225;
    text-transform: uppercase;
	text-align: center;
}
  
.notfound-content h2 {
    font-family: 'Muli', sans-serif;
    font-size: 26px;
    font-weight: 400;
    text-transform: uppercase;
    color: #222225;
    margin-top: 26px;
    margin-bottom: 20px;
}
  
.notfound-content a {
    font-family: 'Muli', sans-serif;
    display: inline-block;
    font-weight: 400;
    text-decoration: none;
    background-color: transparent;
    color: #222225;
    text-transform: uppercase;
    font-size: 14px;
}
  
@media only screen and (max-width: 480px) {
    .notfound-content .notfound-404 {
        height: 146px;
        font-size: 146px;
    }
  
    .notfound-content h2 {
        font-size: 22px;
    }
}
