nav {
  background-color: var(--nav-bg);
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  box-shadow: var(--nav-shadow);
  transition: background-color 0.4s ease-out;
  z-index: 5;
}

nav ul li {
  display: inline;
  padding: 10px;
}
  
.nav-links{
  color: white;
  text-decoration: none;
}
