$color-red: #FF0000;

.home_section img {
    width: 100%;
    min-height: auto;
}

.headline {
    display:block;
    position: relative;
    color: rgb(223, 219, 219);
    font-weight:bold;
    width:100%;
    bottom:35vw;
    font-size:3.8vw;
    background-color: rgba(95, 95, 95, 0.4); /* Black w/opacity/see-through */
}

.flip {
    height:7vw;
    overflow:hidden;
}

.flip > div > div {
    display:inline-block;
    color:#fff;
    padding: 0vw 1vw;
    height:5.5vw;
    margin-bottom:3.5vw;
}
  
.flip div:first-child {
    animation: show 5s linear infinite;
}
  
.flip div div {
    background:#1fe089;
}

.flip div:first-child div {
    background:#4ec7f3;
}

.flip div:last-child div {
    background:#DC143C;
} 

@keyframes show {
    0% {margin-top:-25vw;}
    5% {margin-top:-18vw;}
    33% {margin-top:-18vw;}
    38% {margin-top:-9vw;}
    66% {margin-top:-9vw;}
    71% {margin-top:0vw;}
    99.99% {margin-top:0vw;}
    100% {margin-top:-25vw;}
}


.about_section {
    display: block;
    text-align: left;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 3%;
    margin-top: -15%;
}

.about_section h6 {
    font-size: 20px;
}

.about_section  .custom_heading-container h2{
    margin-top: -10px;
    margin-bottom: -30px;
    font-size: 35px;
}
  
.about_section a {
    display: inline-block;
    padding: 8px 25px;
    background-color: #000000;
    border: 1px solid #000000;
    color: #ffffff;
    border-radius: 25px;
    margin-top: 45px;
    text-decoration: none;
}
  
.about_section a:hover {
    background-color: transparent;
    color: #000000;
}
  
.about_section p {
    margin-top: 45px;
    line-height: 1.5;
    font-weight: 400;
    font-size: 15px;
}

.about_section .img-box img {
    width: 100%;
}
  
.about_section h6 {
    text-transform: uppercase;
    color: #bdbbbb;
    font-weight: bold;
    margin: 1px;
}


.contact_section {
    padding-top: 7vh;
    padding-bottom: 3vh;
}

.contact_section .map-box {
    width: 100%;
    margin-bottom: 400px;
    padding-bottom: 20px;
}

.contact_header {
    font-size: 25px;
    margin-bottom: -30px;
}

.contact_section .form-location {
    display: grid;
    margin-top: 20px;
    padding-top: 20px;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 5%
}

.contact_section .form-location .contact_items {
    display: grid;
    grid-gap: 2rem;
}
 
.contact_section .contact_form-container input {
    border: none;
    outline: none;
    border-bottom: 1px solid #000;
    width: 90%;
    margin: 15px 0;
    background-color: transparent;
}
  
.contact_section .contact_form-container input::-webkit-input-placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_section .contact_form-container input:-ms-input-placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_section .contact_form-container input::-ms-input-placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_section .contact_form-container input::placeholder {
    color: #595959;
    font-size: 14px;
}
  
.contact_section .contact_form-container input.message_input {
    margin-top: 80px;
}
  
.contact_section .contact_form-container button {
    border: none;
    background-color: #000;
    color: #fff;
    font-size: 15px;
    padding: 15px 55px;
    border-radius: 30px;
    text-transform: uppercase;
    border: 1px solid #000000;
    color: #ffffff;
    border-radius: 25px;
}
  
.contact_section .contact_form-container button:hover {
    background-color: transparent;
    color: #000000;
}

.contact_items {
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
  
.contact_items .item {
    width: 250px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
}
  
.contact_items .item .img-box {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-repeat: no-repeat;
    background-position: center;
}
  
.contact_items .item .detail-box {
    color: #5e5e5e;
}
  
.contact_items .item .detail-box p {
    margin: 0;
}
  
.contact_items {
    position: relative;
}
  
.contact_items a {
    position: relative;
}
  
.contact_items .item .img-box.box-1 {
    background-image: url(../images/location.png);
}
  
.contact_items .item .img-box.box-2 {
    background-image: url(../images/telephone.png);
}
  
.contact_items .item .img-box.box-3 {
    background-image: url(../images/envelope.png);
}
